'use strict'



/// login form

const formOpenBtn = document.querySelector('.form__btn-open');
const home = document.querySelector('.top__form');
const formContainer = document.querySelector('.form');
const formCloseBtn = document.querySelector('.form-close');
const singnupBtn = document.querySelector('.form__link--singnup');
const loginBtn = document.querySelector('.form__link--login');


formOpenBtn.addEventListener("click", () => formContainer.classList.add('show'));
formCloseBtn.addEventListener("click", () => formContainer.classList.remove('show'));


singnupBtn.addEventListener("click", (e) => {
    e.preventDefault();
    formContainer.classList.add("active");
});

loginBtn.addEventListener("click", (e) => {
    e.preventDefault();
    formContainer.classList.remove("active");
});

//// password-eye

const password = document.getElementById('password-login');
const passEyeIcon = document.getElementById('pass-icon');



passEyeIcon.onclick = function() {
    if(password.type == 'password'){
        password.type = 'text';
        passEyeIcon.src = "images/eye-open.svg";
    }else{
        password.type = 'password';
        passEyeIcon.src = "images/eye-closed.svg";
    }
}

/////SEARCH///





//// TABS /////

const tabItem = document.querySelectorAll('.tabs__button-item');
const tabContent = document.querySelectorAll('.tabs__content-item');


tabItem.forEach(function (element) {
    element.addEventListener('click', open);
});

function open(evt) {

    const tabTarget = evt.currentTarget;
    const button = tabTarget.dataset.button;

    tabItem.forEach(function (item) {

        item.classList.remove('tabs__button-item--active');
    });

    tabContent.forEach(function (item) {

        item.classList.remove('tabs__content-item--active');
    });

    tabTarget.classList.add('tabs__button-item--active');

    document.querySelector(`#${button}`).classList.add('tabs__content-item--active');
}

